console.log(d3);

// Parse the JSON data file containing the job information
d3.json("data/jobs_20230417.json").then(function(data) {

  console.log(data);

  // Count the number of jobs and store it in a variable
  var jobCount = data.length;
  console.log(jobCount);

  // Parse the job opening dates and store them in an array
  var dateRegex = /jobs_(\d{4})(\d{2})(\d{2})/;
  var matches = dateRegex.exec("data/jobs_20230417.json");
  var date = new Date(parseInt(matches[1]), parseInt(matches[2]) - 1, parseInt(matches[3]));
  var dates = [date];
  console.log(date);
  
  // Create a D3.js line chart using the job count data
  var margin = {top: 20, right: 20, bottom: 30, left: 50},
      width = 960 - margin.left - margin.right,
      height = 500 - margin.top - margin.bottom;
  
  var x = d3.scaleUtc()
      .domain([d3.min(dates), d3.max(dates)])
      .range([margin.left, width - margin.right]);
  
  var y = d3.scaleLinear()
      .domain([0, jobCount])
      .nice()
      .range([height - margin.bottom, margin.top]);
  
  var xAxis = g => g
      .attr("transform", "translate(0," + (height - margin.bottom) + ")")
      .call(d3.axisBottom(x).tickFormat(d3.timeFormat("%b %d")));
  
  var yAxis = g => g
      .attr("transform", "translate(" + margin.left + ",0)")
      .call(d3.axisLeft(y).ticks(10));
  
  var line = d3.line()
      .defined(d => !isNaN(d.value))
      .x(d => x(d.date))
      .y(d => y(d.value));
  
  var svg = d3.select("#chart").append("svg")
      .attr("viewBox", [0, 0, width, height]);
  
  svg.append("g")
      .call(xAxis);
  
  svg.append("g")
      .call(yAxis);
  
  svg.selectAll("path")
    .data([{date: date, value: jobCount}])
    .enter().append("path")
    .attr("fill", "none")
    .attr("stroke", "steelblue")
    .attr("stroke-width", 1.5)
    .attr("d", function(d) {
      return line(d.map(function(job) {
        return {date: job.date, value: job.value};
      }));
    });
  
}).catch(function(error) {
  console.log("Error fetching job data: " + error);
});
